import React, { Component } from 'react';
import Router from 'next/router';

import * as firebase from "firebase";

export default class AddEvent extends Component {
    state = {
        barName: '',
        eventName: '',
        eventPrice: ''
    }

    textInputChanged = (event) => {
        const changedName = event.target.value;
        this.setState( {barName: changedName} );
    }

    writeUserData = (userId, name, email, imageUrl) => {
        // firebase.database().ref('users/' + userId).set({
        //   username: name,
        //   email: email,
        //   profile_picture : imageUrl
        // });
      }

    render() {
        return (
            <div>
                <button className="cancel" onClick={() => Router.push('/')}>X</button>
                <h1>Add Your's event</h1>
                <p>Bar Name<input type="text" onChange={this.textInputChanged} /></p>
                <p>Event Name<input type="text" /></p>
                <p>Event Attendance<input type="text" /></p>
                <button className="submit" onClick={() => this.writeUserData()}>ADD EVENT</button>
            </div>
        );
    }
}


